import { Store, useStore } from "./Store";
import getinstance from "../utils/api/getinstance";
import editinstance from "../utils/api/editinstance";
import postinstance from "../utils/api/postinstance";
import deleteinstance from "../utils/api/deleteinstance";
export class instanceItem {

    id: String;
    i_icon: String;
    mascot: String;
    i_title: String;
    i_stitle: String;
    i_body: String;
    i_bodytype: String;
    i_bodyoptions: String;
    locations: String;
    i_media: String;
    i_misc: String;
    created_at: String;
    updated_at: String;
    color_field: String;

    constructor(o: {
        id: String;
        i_icon: String;
        mascot: String;
        i_title: String;
        i_stitle: String;
        i_body: String;
        i_bodytype: String;
        i_bodyoptions: String;
        locations: String;
        i_media: String;
        i_misc: String;
        created_at: String;
        updated_at: String;
        color_field: String;
    }) {
        this.id = o.id;
        this.i_icon = o.i_icon;
        this.mascot = o.mascot;
        this.i_title = o.i_title;
        this.i_stitle = o.i_stitle;
        this.i_body = o.i_body;
        this.i_bodytype = o.i_bodytype;
        this.i_bodyoptions = o.i_bodyoptions;
        this.locations = o.locations;
        this.i_media = o.i_media;
        this.i_misc = o.i_misc;
        this.created_at = o.created_at;
        this.updated_at = o.updated_at;
        this.color_field = o.color_field;
    }
}

export const instanceStore = new Store<instanceItem[] | false>(false);

export const instancestore = () => {
    const [instance, setinstance] = useStore(instanceStore);

    return {
        instance, setinstance,

        async loadinstance() {
            await getinstance("").then(
                (data) => {
                    setinstance(data.data.results);
                }
            )
        },
        async loadinstancewithid(id: any) {
            await getinstance(id).then(
                (data) => {
                    setinstance(data.data.results);
                }
            )

        },
        async postinstance(data: any) {
            await postinstance(data).then((res) => {
                console.log(res);
                setinstance((oldArray: any) => [...oldArray, res.data]);
            }).catch((err) => {
                console.log(err);
            })
        },
        async editinstance(id: any, data: any) {
            await editinstance(id, data).then((res) => {
                console.log(res);
                setinstance((oldarr) => {
                    return (oldarr) && oldarr.map((obj: any) => {
                        const getobj = (o: any) => {
                            let x = o;
                            x.i_icon = data.i_icon
                            x.mascot = data.mascot
                            x.i_title = data.i_title
                            x.i_stitle = data.i_stitle
                            x.i_body = data.i_body
                            x.i_bodytype = data.i_bodytype
                            x.i_bodyoptions = data.i_bodyoptions
                            x.locations = data.locations
                            x.i_media = data.i_media
                            x.i_misc = data.i_misc
                            x.color_field = data.color_field
                            return x;
                        }
                        return (String(obj.id) === String(id)) ? getobj(obj) : obj;
                    })
                })
            }).catch((err) => {
                console.log(err);
            })
        },
        async deleteinstance(id: any) {
            await deleteinstance(id).then((res) => {
                console.log(res);
                setinstance(instance && instance.filter(ele => ele.id !== id))
            }).catch((res) => {
                console.log(res);
            })
        }
    }
}